.elements {
  display: grid;
  width: 100%;
  grid-template-columns: repeat(3, auto);
  grid-template-rows: auto;
  grid-column-gap: 17px;
  -webkit-column-gap: 17px;
          column-gap: 17px;
  grid-row-gap: 30px;
  row-gap: 30px;
}

@media screen and (min-width: 751px) and (max-width: 900px) {
  .elements {
    grid-template-columns: repeat(2, 282px);
    max-width: calc(282px * 2 + 21px);
  }
}
@media screen and (max-width: 750px) {
  .elements {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 36px;
    width: 370px;
  }
}
@media screen and (max-width: 500px) {
  .elements {
    width: 320px;
  }
}

.footer {
  align-self: flex-start;
  margin: 65px 0 60px 0;
}
.footer__copyright {
  font-family: "Inter", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #545454;
  margin: 0;
}
@media screen and (max-width: 900px) {
  .footer {
    margin: 50px auto 36px auto;
    width: 585px;
  }
  .footer__copyright {
    font-size: 20px;
  }
}
@media screen and (max-width: 750px) {
  .footer {
    width: 282px;
  }
}
@media screen and (max-width: 500px) {
  .footer__copyright {
    font-size: 14px;
  }
}

.header {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.header__vector {
  width: 173px;
  height: 33px;
  margin: 45px 0 41px 0;
}
.header__line {
  opacity: 0.7;
  flex-grow: 1;
  margin: 0;
  height: 0;
  border-bottom: 1px solid #545454;
}

@media screen and (max-width: 900px) {
  .header__vector {
    margin-left: 1%;
    margin-bottom: 15px;
    width: 350px;
    height: 45px;
  }
}
@media screen and (min-width: 426px) and (max-width: 500px) {
  .header__vector {
    align-self: center;
    margin-bottom: 25px;
  }
}

@media screen and (max-width: 425px) {
  .header__vector {
    width: 126px;
    height: 24px;
    margin: 28px 0 32px 27px;
  }
}

.page {
  background-color: black;
}
.page__content {
  position: relative;
  margin: 0 auto;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 880px;
  box-sizing: border-box;
}

.profile {
  margin: 40px 0 50px 0;
  width: 100vw;
  max-width: 880px;
  height: 120px;
  display: flex;
  position: relative;
}
.profile__avatar-image {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  margin-right: 30px;
  object-fit: cover;
}
.profile__overlay {
  position: absolute;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  object-fit: cover;
  background-image: url(/around-react/static/media/editButton.58afbe73.svg);
  background-size: 20%;
  background-repeat: no-repeat;
  background-color: rgba(0, 0, 0, 0.8);
  border: none;
  background-position: center;
  opacity: 0;
  transition: 0.3s;
}
.profile__overlay:hover {
  opacity: 1;
  cursor: pointer;
}

.profile__info {
  display: flex;
  flex-direction: column;
  margin-bottom: 21px;
  height: 100%;
}
.profile__top-info {
  display: flex;
}
.profile__name {
  margin: 22px 18px 0 0;
  max-height: 39px;
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 42px;
  line-height: 114%;
  /* or 114% */
  color: #ffffff;
}
.profile__edit-button {
  width: 24px;
  height: 24px;
  margin: 37px 0 0 0;
  border: 1px solid #ffffff;
  background-color: #0000;
  background-image: url(/around-react/static/media/editButton.58afbe73.svg);
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  transition: 0.3s;
}
.profile__edit-button:hover {
  opacity: 0.6;
}
.profile__description {
  margin: 16px 0 0 0;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 137.5%;
  /* identical to box height */
  color: #ffffff;
}
.profile__add-button {
  margin-top: 35px;
  margin-left: auto;
  width: 150px;
  height: 50px;
  border: 2px solid #ffffff;
  border-radius: 2px;
  background-color: #000;
  background-image: url(/around-react/static/media/addButton.bdfb8ebb.svg);
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  transition: 0.3s;
}
.profile__add-button:hover {
  opacity: 0.6;
}

@media screen and (min-width: 751px) and (max-width: 900px) {
  .profile {
    display: grid;
    margin-bottom: 0;
    height: 200px;
    grid-template-columns: repeat(2, auto);
    grid-template-rows: repeat(2, 120px);
    grid-template-areas:
      "img info"
      ". button";
    max-width: 734px;
  }
  .profile__info {
    height: unset;
    margin-left: auto;
    margin-right: auto;
  }
  .profile__avatar-image {
    margin: 0 auto;
    width: 160px;
    height: 160px;
  }
  .profile__overlay {
    width: 160px;
    height: 160px;
    left: 6.7%;
  }
  .profile__name {
    font-size: 35px;
    margin: 0;
  }
  .profile__edit-button {
    margin: 5px auto 0 20px;
    width: 35px;
    height: 30px;
  }
  .profile__description {
    margin: 10px 0 0 0;
  }
  .profile__add-button {
    grid-area: button;
    margin: 0 auto 0 auto;
    width: calc(330px + 20px + 1px + 6px + 21px);
    padding: 0;
  }
}
@media screen and (max-width: 750px) {
  .profile {
    height: unset;
    width: 282px;
    margin: 42px auto 0 auto;
    flex-direction: column;
    align-items: center;
  }
  .profile__avatar-image {
    margin: 0 100px;
    width: 160px;
    height: 160px;
  }
  .profile__overlay {
    width: 160px;
    height: 160px;
    left: 21.7%;
  }

  .profile__info {
    display: block;
    text-align: center;
    margin: 26.25px 0 36px 0;
    max-height: 50px;
  }
  .profile__name {
    font-size: 25px;
    margin: 0;
  }
  .profile__edit-button {
    width: 18px;
    height: 18px;
    margin: 0 0 0 10px;
  }
  .profile__description {
    font-size: 14px;
    margin-top: 14px;
  }
  .profile__add-button {
    width: 282px;
    height: 50px;
    margin: 0;
    background-size: 16px;
  }
}
@media screen and (max-width: 500px) {
  .profile {
    margin: 42px 19px 0 19px;
    flex-direction: column;
    align-items: center;
  }
  .profile__name {
    font-size: 22px;
  }
  .profile__overlay {
    width: 190px;
    height: 190px;
    left: 15%;
    top: -5px;
  }
}

.card {
  width: 282px;
  max-height: 361px;
  border-radius: 10px;
  background-color: #ffffff;
  position: relative;
}
.card__image {
  width: 100%;
  height: 282px;
  border-radius: 10px 10px 0 0;
  cursor: pointer;
}
.card__caption {
  margin: 25px 0 0 21px;
  max-width: 220px;
  font-family: "Inter";
  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  color: #000000;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.card__like {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.card__like-counter {
  margin: 2px 0 0 6px;
}
.card__like-button {
  background: none;
  height: 19px;
  width: 21px;
  margin-right: 18px;
  margin-top: 30px;
  padding: 0;
  border: none;
  background-image: url(/around-react/static/media/likeButton.94185013.svg);
  background-size: contain;
  background-repeat: no-repeat;
  cursor: pointer;
  transition: 0.2s;
}
.card__like-button:hover {
  opacity: 0.5;
}
.card__like-button_active {
  background-image: url(/around-react/static/media/likeButton_active.a5ea67e3.svg);
}
.card__social-brand {
  display: flex;
  background: #ffffff;
  border-radius: 0 0 10px 10px;
  max-height: 79px;
  justify-content: space-between;
  width: 100%;
  padding-bottom: 9%;
}
.card__removeButton {
  position: absolute;
  top: 18px;
  right: 15px;
  width: 18px;
  height: 19.3px;
  background: none;
  background-image: url(/around-react/static/media/deleteButton.7f080e1c.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border: none;
  cursor: pointer;
  transition: 0.3s;
}
.card__removeButton_hidden {
  opacity: 0;
  visibility: hidden;
}
.card__removeButton:hover {
  opacity: 0.6;
}

@media screen and (max-width: 768px) {
  .card__like-button_active:hover {
    /* There is no need for hover state on mobile devices while active */
    opacity: 1;
  }
}

.popup_image {
  position: fixed; /* Stay in place */
  display: flex;
  flex-direction: column;
  z-index: 1;
  width: 100vw;
  height: 100vh;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.5); /* Black w/ opacity */
  transition: all 0.3s linear;
  visibility: hidden;
  opacity: 0;
  align-items: center;
  justify-content: center;
}
.popup_image .popup__wrapper {
  /*This div make the closing button responsive*/
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}
.popup__image {
  max-width: 75vw;
  max-height: 75vh;
}

.popup__caption {
  margin: 10px 0 0 0;
  font-family: "Inter", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  color: #ffffff;
  align-self: flex-start;
}

.popup__close-button_image {
  height: 32px;
  width: 32px;
  position: absolute;
  right: -35px;
  top: -35px;
  padding: 0;
  cursor: pointer;
  border: none;
  background-color: rgba(0, 0, 0, 0);
  background-image: url(/around-react/static/media/closeIcon.58430b35.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  transition: 0.2s;
}

@media screen and (max-width: 650px) {
  .popup__close-button_image {
    max-height: 20px;
    max-width: 20px;
    top: -25px;
    right: -15px;
  }
  .popup_image {
    width: 100%;
    height: 100%;
  }
  .popup_image .popup__wrapper {
    max-width: 75vw;
  }
}

.popup {
    position: fixed;
    display: flex;
    flex-direction: column;
    z-index: 2;
    width: 100vw;
    height: 100vh;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.5); /* Black w/ opacity */
    align-items: center;
    visibility: hidden;
    opacity: 0;
    transition: all 0.3s linear;
}
.popup .popup__wrapper {
    /*This div make the closing button responsive*/
    margin-top: 180px;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.popup__form {
    position: relative;
    display: flex;
    flex-direction: column;
    max-width: 430px;
    min-height: 100%;
    background: #ffffff;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    margin: 0;
}
.popup__wrapper_type_confirm {
    position: relative;
    display: flex;
    flex-direction: column;
    max-width: 430px;
    height: 181px;
    background: #ffffff;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    margin: 0;
}
.popup__wrapper_profile-image {
    height: 272px;
}
.popup__form_profile-image {
    height: 15px;
}
.popup__close-button {
    height: 32px;
    width: 32px;
    position: absolute;
    right: -35px;
    top: -35px;
    padding: 0;
    cursor: pointer;
    border: none;
    background-color: rgba(0, 0, 0, 0);
    background-image: url(/around-react/static/media/closeIcon.58430b35.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    transition: 0.2s;
}
.popup__close-button:hover {
    opacity: 0.6;
}
.popup__title {
    font-family: "Inter", sans-serif;
    width: 330px;
    max-height: 23px;
    margin: 34px 64px 54px 36px;
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    color: #000000;
}
.popup__input {
    position: relative;
    border: none;
    width: 358px;
    margin: 0 0 0 36px;
    font-family: "Inter", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    color: #000000;
    padding-top: 6.5px;
    padding-bottom: 6.5px;
    text-indent: 5px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}
.popup__input:nth-last-child(2) {
    margin-bottom: 0;
}
.popup__line {
    border: none;
    margin-top: 13px;
    opacity: 0.2;
    border-bottom: 1px solid #000000;
    width: 358px;
    margin-bottom: 0;
}

.popup__submit {
    align-self: center;
    margin-bottom: 30px;
    width: 358px;
    height: 50.15px;
    background: #000000;
    border-radius: 2px;
    border: none;
    font-family: "Inter", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    text-align: center;
    color: #ffffff;
    cursor: pointer;
    padding: 0;
    transition: 0.3s;
}
.popup__submit:hover {
    opacity: 0.8;
}
.popup__submit-button_inactive {
    opacity: 0.2;
    cursor: auto;
}
.popup__submit-button_inactive:hover {
    opacity: 0.2;
}

.popup_show {
    transition: 0.3s;
    visibility: visible;
    opacity: 1;
}
.popup__field {
    display: flex;
    flex-direction: column;
    margin-bottom: 13px;
}
.popup__input-error {
    margin: 5px 0 0 36px;
    font-family: "Inter";
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    color: #ff0000;
    visibility: hidden;
    opacity: 0;
}
.popup__input-error_active {
    visibility: visible;
    opacity: 1;
}
.popup__input_type_error {
    border-bottom-color: red;
}

