    }
    .popup__input {
        width: 238px;
        margin: 0 0 30px 22px;
        max-height: 14px;
        padding-bottom: 9px;
    }
    .popup__line {
        width: 238px;
        margin-top: 13px;
    }

    .popup__input:nth-last-child(2) {
        margin-bottom: 0;
    }
    .popup__submit {
        position: absolute;
        width: 238px;
        height: 46px;
        margin: 0;
        font-size: 14px;
        padding: 0;
        bottom: 30px;
    }
    .popup__input-error {
        margin: 5px 0 0 22px;
    }
}



